@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.hero-enter{opacity:0}.hero-enter-active{opacity:1;transition:opacity 500ms ease-in}.hero-exit{opacity:1}.hero-exit-active{opacity:0;transition:opacity 500ms ease-in}
.app{height:100vh;overflow-y:scroll;background:url(/static/media/hero_bg.0540b794.jpg) center/cover no-repeat}.content{display:grid;grid-template-columns:560px 440px;max-width:1040px;margin:0 auto;padding-top:50px}.content__interactive{padding-left:40px}@keyframes fade{0%{opacity:0}100%{opacity:1}}.rounded-pill{opacity:0}.card:hover .rounded-pill{animation:.4s cubic-bezier(0.25, 0.25, 0.25, 1.25) forwards fade;opacity:1}@media(min-width: 320px)and (max-width: 786px){.content{grid-template-columns:repeat(auto-fill, minmax(300px, 1fr));grid-gap:2vw;max-width:100%}.content__interactive{padding-left:0}.start-100{left:94.5% !important}.top-0{top:20px !important}}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto",sans-serif}ul{padding:0}
